@import url(https://fonts.googleapis.com/css?family=Charm);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 98%;
  overflow: hidden;
  margin: 0;
}
body{
  background-color: #cb2d3e;
    color: #b1b1b1;
    background: -webkit-radial-gradient(center, ellipse, #111 10%, #ef473a 100%);
    background: radial-gradient(ellipse at center, #111 10%, #ef473a 100%);
    background-attachment: fixed;
  background-color: red;
  color: white;
}
/* import cursive 'santa' font */


.navbar{
  font-family: 'Charm', cursive;
  font-size: 1.5em;
  height: 10%;
  border-bottom: 3px solid green;
}

.navbar button, .logoutButton {
	box-shadow:inset 0px 39px 0px -24px #3dd28d;
	background-color:#3dd28d;
	border-radius:4px;
	border:1px solid #ffffff;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	padding:6px 15px;
	text-decoration:none;
	text-shadow:0px 1px 0px #3dd28a;
}
.myButton:hover {
	background-color:#eb675e;
}
.myButton:active {
	position:relative;
	top:1px;
}

.nav-ul{
  display: flex;
  list-style-type: none;
  justify-content: space-evenly;
}

.nav-login{
  z-index: 7;
}

.image-container {
  display: flex;
  position: absolute;
  justify-content: space-evenly;
  height: 100%;
}
.upvoted-image-column{
  float:left;
display: flex;
flex-direction: column;
width: 30%;
max-height: 100%;
overflow: scroll;
}
.upvoted-image{
width: 100%;
}
.button-row{
  display: flex;
  justify-content: space-evenly;
padding-bottom: 1em;

}

.fancy-border {
  border: 2px solid white;
}

.column-text{
  font-size: 1.5em;
  font-family: 'Charm', cursive;
  text-align: center;
}
